import { AppBar, Box, CardContent, createMuiTheme, CssBaseline, Dialog, DialogContent, IconButton, makeStyles, ThemeProvider, Toolbar, Typography } from '@material-ui/core'
import deepOrange from '@material-ui/core/colors/deepOrange'
import deepPurple from '@material-ui/core/colors/deepPurple'
import CloseIcon from '@material-ui/icons/Close'
import GitHubIcon from '@material-ui/icons/GitHub'
import MenuIcon from '@material-ui/icons/Menu'
import React from 'react'

import GridDivider from './GridDivider'
import Menu from './Menu'

const theme = createMuiTheme({
  typography: {
    fontFamily: 'Nunito, sans-serif',
    fontSize: 14,
  },
  palette: {
    primary: {
      main: deepOrange.A400,
      contrastText: '#fff',
    },
    secondary: {
      main: deepPurple[600],
      contrastText: '#fff',
    },
  },
  overrides: {
    MuiCssBaseline: {
      '@global': {
        body: {
          backgroundColor: '#f5f5f5',
        },
      },
    },
  },
})

interface Props {
  title?: string
}

const useStyles = makeStyles((theme) => ({
  menuButton: {
    marginRight: theme.spacing(2),
  },
}))

const Layout = ({ title = 'Home Page', children }: React.PropsWithChildren<Props>) => {
  const { menuButton } = useStyles()
  const [open, setOpen] = React.useState(false)

  const openGithub = React.useCallback(() => {
    if (typeof window === 'undefined') { return }
    window.open('https://github.com/ericchanky/ericchanky.github.io', '__blank')
  }, [])

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <AppBar>
        <Toolbar>
          <IconButton
            color="inherit"
            className={menuButton}
            onClick={() => setOpen(true)}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h5" component="h1">{title}</Typography>
          <GridDivider />
          <IconButton
            color="inherit"
            onClick={openGithub}
          >
            <GitHubIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      <Box component="main">
        <Toolbar />
        <CardContent>
          {children}
        </CardContent>
      </Box>
      <Dialog
        fullScreen
        open={open}
        onClose={() => setOpen(false)}
      >
        <AppBar elevation={0} color="inherit" position="relative">
          <Toolbar>
            <GridDivider />
            <IconButton color="inherit" onClick={() => setOpen(false)}>
              <CloseIcon />
            </IconButton>
          </Toolbar>
        </AppBar>
        <DialogContent>
          <Menu />
        </DialogContent>
      </Dialog>
    </ThemeProvider>
  )
}

export default Layout
